@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
body {
  margin: 0;
}
img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.main-page {
  flex-grow: 1;
}
.page {
  height: 100%;
  font-size: 125%;
  line-height: 1;
}
.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  min-width: 320px;
  width: 100%;
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 0.7rem;
  line-height: calc(18 / 14);
  font-weight: 400;
  color: #444444;
  counter-reset: features;
}
@media (min-width: 768px) {
  .page__body {
    font-size: 0.8rem;
    line-height: 1.5;
  }
}
.button {
  display: block;
  padding: 7px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  background-color: #68b738;
  border: none;
  cursor: pointer;
}
.button--secondary {
  padding: 10px;
  font-size: 0.8rem;
  line-height: 1.25;
  color: #444444;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .button {
    font-size: 1rem;
    line-height: 1;
  }
  .button--secondary {
    font-size: 0.8rem;
    line-height: 1.25;
  }
}
.main-header__logo-wrapper {
  padding: 10px 20px 12px;
  border-bottom: 1px solid #e6e6e6;
}
.main-header__logo {
  display: block;
  width: 230px;
}
.main-header__cat-logo:hover {
  opacity: 0.8;
}
.main-header__cat-logo:active {
  opacity: 0.6;
}
.main-header__cat-logo--tablet,
.main-header__cat-logo--desktop {
  display: none;
}
@media (min-width: 768px) {
  .main-header {
    display: flex;
    align-items: center;
    padding: 24px 30px 63px;
  }
  .main-header__logo-wrapper {
    padding: 0;
    border: none;
  }
  .main-header__cat-logo--mobile {
    display: none;
  }
  .main-header__cat-logo--tablet {
    display: block;
  }
}
@media (min-width: 1440px) {
  .main-header {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    max-width: 1440px;
    margin: 0 auto;
    padding: 53px 110px 60px;
  }
  .main-header__cat-logo--tablet {
    display: none;
  }
  .main-header__cat-logo--desktop {
    display: block;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 24px;
  right: 20px;
  z-index: 2;
  display: block;
  width: 24px;
  height: 16px;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
.main-nav--closed .main-nav__toggle::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  content: "";
}
.main-nav--opened .main-nav__toggle {
  top: 23px;
  right: 24px;
  width: 18px;
  height: 18px;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  position: absolute;
  top: 8px;
  right: -3px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  content: "";
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__item a[href]:hover {
  opacity: 0.6;
}
.main-nav__item a[href]:active {
  opacity: 0.3;
}
.main-nav__link {
  display: block;
  padding: 22px 0 24px;
  font-size: 1rem;
  line-height: 1;
  font-weight: 500;
  color: #000000;
  text-align: center;
  text-transform: uppercase;
  border-bottom: 1px solid #e6e6e6;
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-nav {
    margin-left: auto;
  }
  .main-nav__toggle {
    display: none;
  }
  .main-nav__wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  .main-nav__list {
    display: flex;
    justify-content: flex-end;
    padding-top: 9px;
  }
  .main-nav__item {
    border-top: none;
  }
  .main-nav__item:not(:last-of-type) {
    margin-right: 34px;
  }
  .main-nav__item--current {
    position: relative;
  }
  .main-nav__item--current::after {
    position: absolute;
    right: 0;
    left: 0;
    bottom: -9px;
    height: 2px;
    background-color: #68b738;
    content: "";
  }
  .main-nav__link {
    padding: 0;
    font-size: 0.9rem;
    line-height: calc(24 / 18);
    font-weight: 400;
    border: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__item--secondary::after {
    bottom: -8px;
    background-color: #ffffff;
  }
  .main-nav__link {
    font-size: 1rem;
    line-height: 1.5;
  }
  .main-nav__link--secondary {
    color: #ffffff;
  }
}
.promo {
  position: relative;
  text-align: center;
  color: #ffffff;
}
.promo::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 302px;
  background-image: url("../img/index-promo-bg-mobile.jpg");
  background-color: #64a73d;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  z-index: -1;
  content: "";
}
@media (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/index-promo-bg-mobile@2x.jpg");
  }
}
@media (min-width: 768px) {
  .promo::before {
    display: none;
  }
}
@media (min-width: 1440px) {
  .promo::before {
    display: block;
    top: -25%;
    left: 50%;
    width: 720px;
    height: 708px;
    background-image: url("../img/index-promo-bg-desktop.jpg");
    content: "";
  }
}
@media (min-width: 1440px) and (min-resolution: 2dppx) {
  .promo::before {
    background-image: url("../img/index-promo-bg-desktop@2x.jpg");
  }
}
